body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative !important;
  max-width: 100% !important;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a, button {
  text-decoration: none !important;
  color: #fff !important;
  background-color: #9c27b0;
  border-radius: 69px !important;
  padding: 10px 22px;
  cursor: pointer;
  z-index: 101 !important;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;;
}
a:hover,button:hover{
  background-color: #692275;
}
div{
  font-family: 'Roboto Mono', monospace;
}
h1{
  font-family: 'Oswald', sans-serif;
  margin-top: 0px;
  margin-bottom: 16px;
}
h4{
  text-decoration: underline;
}
.logo{
  margin-top:10px;
}
.card{
  margin-top:1%;
}
.input-text{
  max-width:35%;
  margin-left:auto;
  margin-right:auto;
}
.loader{
  margin-top:15%;
  width: 10%;
}
.footer-main{
  margin-top: 2%;
  padding-top: 2px;
  border-top: 4px solid rgb(183 110 182);
}
.footer{
  display: flex;
  justify-content: space-evenly;
  position: absolute;
  width: 100%;
  height: 96vh;
  color: #fff;
  background-color: rgb(64 37 68);

}
.footer-list{
  display:flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.header{
  display: flex;
  justify-content: space-between;
  /* overflow: hidden; */
  align-items: center;
  width: 100%;
  height: 8vh;
  background-color: rgb(183 110 182);
  color: white;
}
.container{
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
 
}
.logo{
  max-width: 200px;
  max-height: 50px;
  margin-top: 0%;
}
.center{
  text-align: center;
}
.home-main{
  z-index: 20;
  position: absolute;
  top:30%;
  text-align: center;
  left: 30%;
  color: aliceblue;
}
.home-button{
  padding-right: 20%;
  padding-left: 20%;
  padding-top: 0%;
}
@keyframes slide-left {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
}
.container-slider{
  display: flex;
  overflow: hidden;
}
.container-products{
  display: flex;
  animation: slide-left 35s linear infinite;
}
.section{
  text-align: center;
  left:30%;
  padding-left: 25%;
  padding-right: 25%;
  padding-top: 2%;
  padding-bottom: 5%;
  border-bottom: 4px solid rgb(183 110 182);
  border-top: 4px solid rgb(183 110 182);
}
.footer-logo{
  display: flex;
  align-items: center;
}
.login, .signup{
  padding-bottom: 26vh;
}
.profile{
  padding-bottom: 31.5vh;
}
.mySelect{
  background: #36a9a3;
  color: #fff;
  padding: 0 10px;
  font-size: 16px;
  appearance: none;
  font-family: 'Roboto Mono', monospace;
}
select.mySelect option{
color: rgb(255, 255, 255);
  padding: 0 10px;
}